<template>
    <div>
        <form @submit.prevent="addSubforum">
            <label for="name">Name:</label>
        <input type="text" id="name" v-model="name" placeholder="Name" name="name"><br>
            <label for="description">Description:</label>
        <input type="text" id="description" v-model="description" placeholder="Description" name="description"><br>
            <label for="rules">Rules:</label>
        <input type="text" id="rules" v-model="rules" placeholder="Rules" name="rules"><br>
            <label for="icon">Icon:</label>
        <input type="file" id="icon" v-on:change="icon" placeholder="Icon" name="icon"><br>
        <input type="submit" value="Submit">
    </form>
    </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      name: '',
      description: '',
      rules: '',
      icon: ''
    }
  },
  methods : {
    addSubforum () {
      axios.post('http://localhost:8084/forum/rest/subforumService/subforums', {
        params: {
        name: this.name,
        description: this.description,
        rules: this.rules,
        icon: this.icon
      },
        headers: {
          'Content-type': 'multipart/form-data'
        }}).then (response => {
        console.log(response);
      }).catch (error => {
        console.log(error);
      })
    }
  }
}
</script>

<style>
</style>



