<template>
  <div>
    <form class="signup-form" @submit.prevent="signup">
      <label for="username">Username:</label>
      <input v-model="username" id="username" type="text" autofocus="true" minlength="4" maxlength="20" size="50" placeholder="Please input desired username"><br>
      <label id="username" for="password">Password:&nbsp;</label>
      <input v-model="password" id="password" type="password" minlength="4"  maxlength="40" size="50" placeholder="Please input desired password"><br>
      <label for="firstName">First name:</label>
      <input v-model="firstName"id="firstName" type="text" minlength="2" maxlength="20" size="50" placeholder="Please enter Your first name"><br>
      <label for="lastName">Last name:</label>
      <input v-model="lastName" id="lastName" type=" text"minlength="3"  maxlength="20" size="50" placeholder="Please enter Your last name"><br>
      <label for="phone">Phone:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
      <input v-model="phone"id="phone" type="tel" minlength="9" maxlength="13" size="50"  placeholder="Please enter your phone number"><br>
      <label for="email">Email:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
      <input v-model="email" id="email" type="email"  maxlength="40" size="50" placeholder="Please enter your email"><br><br>
      <input type="submit" value="Submit">
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      password: '',
      firstName: '',
      lastName: '',
      phone: '',
      email: '',
      errors: []
    }
  },
  methods: {
    signup() {
      let params = new URLSearchParams();
      let phoneNumber = parseInt(this.phone);

      params.append('username', this.username);
      params.append('password', this.password);
      params.append('firstName', this.firstName);
      params.append('lastName', this.lastName);
      params.append('phone',  phoneNumber);
      params.append('email', this.email);

      axios.post('http://localhost:8084/forum/rest/userService/users',params)
        .then (response => {
          this.$router.replace(this.$route.query.redirect || '/login')
          console.log(response);
        }).catch ( error => {
          this.errors.push(error);
          console.log(error);
        })
    }
  }
}
</script>

<style scoped>
.signup-form {
  font-weight: normal;
  display:  inline-block;
  margin: 0 10px;
  text-align: left;
}
input {
  size: 50;
  text-align: center;
}
</style>



