import axios from 'axios';

export default {

  login (username, password, cb) {
    cb = arguments[arguments.length-1]
    if (localStorage.token) {
      if (cb) cb(true)
      this.onChange(true)
      return
    }
    pretendRequest(username, password, (res) => {
      if (res.authenticated) {
        localStorage.token  = res.token
        if (cb) cb(true)
        this.onChange(true)
      } else {
        if (cb) cb(false)
        this.onChange(false)
        }
    })
  },

  getToken () {
    return localStorage.token
  },

  logout (cb) {
    delete localStorage.token
    if (cb) cb()
    this.onChange(false)
  },

  loggedIn () {
    return !!localStorage.token
  },

  onChange () {}
}

function pretendRequest (username, password, cb) {
      let params = new URLSearchParams();

      params.append('username', username);
      params.append('password', password);

      console.log(params);
      axios.post('http://localhost:8084/forum/rest/userService/login', params)
        .then(response => {
          console.log(response);
      if (username  === response.data.username && password === response.data.password) {
        cb({ authenticated: true,
        token: Math.random().toString(36).substring(7)
      })
  } else {
      cb({authenticated: false})
    }
    }).catch(error => {
        this.errors.push(error);
        console.log(error);
    });
}



